import React, { Component } from 'react'
import Toast from './components/toast'

class ToastDemo extends Component {
    render() {
        return (
            <div className="App">
                <button onClick={() => { Toast.info('') }}></button>
                <button onClick={() => { Toast.success('') }}></button>
                <button onClick={() => { Toast.warning('') }}></button>
                <button onClick={() => { Toast.error('') }}></button>
                <button
                    onClick={() => {
                        const hideLoading = Toast.loading('...',0, () => {
                            Toast.success('')
                        })
                        setTimeout(hideLoading, 2000)
                    }}
                >
                </button>
            </div>
        )
    }
}

export default ToastDemo


